@import '../App.scss';
.lds-circle {
    display: inline-block;
    transform: translateZ(1px);
}

.lds-circle>div {
    display: inline-block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    background: #fff;
    animation: lds-circle 2.4s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}

@keyframes lds-circle {
    0%,
    100% {
        animation-timing-function: cubic-bezier(0.5, 0, 1, 0.5);
    }
    0% {
        transform: rotateY(0deg);
    }
    50% {
        transform: rotateY(1800deg);
        animation-timing-function: cubic-bezier(0, 0.5, 0.5, 1);
    }
    100% {
        transform: rotateY(3600deg);
    }
}

.card-wrapper {
    width: 40rem;
    min-height: 25rem;
    max-height: 40rem;
    background-color: $dark-grayish-blue;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    border-radius: 1.2rem;
    position: relative;
    .text-wrapper {
        width: 32rem;
        height: 15rem;
        margin-top: 2.5rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .advice-number {
            color: $neon-green;
            letter-spacing: 0.2rem;
            font-size: 0.8rem;
            font-weight: lighter;
        }
        .advice-content {
            color: $light-cyan;
            text-align: center;
            font-size: 1.5rem;
            h2 {
                font-size: 2rem;
            }
        }
    }
    .dice-button {
        width: 4rem;
        height: 4rem;
        border: none;
        background-color: $neon-green;
        border-radius: 50%;
        position: absolute;
        top: 23.2rem;
        right: 17.9rem;
        border: 1px solid black;
        outline: none;
        border-radius: 50%;
        padding: 8px 4px;
        color: white;
    }
}